import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import Reader from '../views/Reader.vue'
import Periodicals from '../views/Perodicals.vue'
import Book from '../views/Book.vue'
import ReadNow from '../views/ReadNow.vue'
import Employee from '../views/Employee.vue'
import addbook from '../views/AddBook.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Login',
    component: Login,
    meta:{
      title: "Library - Login"
    }
  },
  {
    path: '/main',
    name: 'Home',
    component: Home,
    meta:{
      title: "Library - Main"
    }
  },
  {
    path: '/reader',
    name: 'Reader',
    component: Reader,
    meta:{
      title: "Library - Reader"
    }
  },
  {
    path: '/periodicals',
    name: 'Periodicals',
    component: Periodicals,
    meta:{
      title: "Library - Reriodicals"
    }
  },
  {
    path: '/periodical/:id',
    name: 'Book',
    component: Book,
    meta:{
      title: "Library - Periodical"
    }
  },
  {
    path: '/readnow',
    name: 'ReadNow',
    component: ReadNow,
    meta:{
      title: "Library - read now"
    }
  },
  {
    path: '/employee',
    name: 'Employee',
    component: Employee,
    meta:{
      title: "Library - Employee"
    }
  },
  {
    path: '/add',
    name: 'addbook',
    component: addbook,
    meta:{
      title: "Library - add book"
    }
  },
  
  

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})


router.beforeEach((to, from, next) => {
  const nearestWithTitle = to.matched.slice().reverse().find(r => r.meta && r.meta.title);
  const nearestWithMeta = to.matched.slice().reverse().find(r => r.meta && r.meta.metaTags);
  const previousNearestWithMeta = from.matched.slice().reverse().find(r => r.meta && r.meta.metaTags);
  if(nearestWithTitle) {
    document.title = nearestWithTitle.meta.title;
  } else if(previousNearestWithMeta) {
    document.title = previousNearestWithMeta.meta.title;
  }
  Array.from(document.querySelectorAll('[data-vue-router-controlled]')).map(el => el.parentNode.removeChild(el));
  if(!nearestWithMeta) return next();
  nearestWithMeta.meta.metaTags.map(tagDef => {
    const tag = document.createElement('meta');

    Object.keys(tagDef).forEach(key => {
      tag.setAttribute(key, tagDef[key]);
    });

    tag.setAttribute('data-vue-router-controlled', '');

    return tag;
  })
      .forEach(tag => document.head.appendChild(tag));
  next();
});


export default router
